body {
  background: #222; }

.container {
  bottom: 0;
  height: 120px;
  left: 0;
  margin: auto;
  right: 0;
  position: absolute;
  top: 0;
  width: 120px; }

.planet {
  background: #ff3411;
  border-radius: 50%;
  box-shadow: inset -20px -10px 0 rgba(0, 0, 0, 0.25);
  height: 100%;
  overflow: hidden;
  position: absolute;
  width: 100%;
  z-index: 0;
  -webkit-animation: disco 10s ease-in-out infinite alternate;
  -moz-animation: disco 10s ease-in-out infinite alternate;
  animation: disco 10s ease-in-out infinite alternate; }
  .planet:after {
    background-image: url(https://s3.amazonaws.com/kunalbhat/bg.png);
    background-position: 0px 0px;
    background-repeat: repeat-x;
    content: "";
    display: inline-block;
    height: 120px;
    left: 0;
    position: relative;
    width: 1810px;
    -webkit-animation: planet-fx 40s linear infinite;
    -moz-animation: planet-fx 40s linear infinite;
    animation: planet-fx 40s linear infinite; }

.moon {
  background-color: #c9c9c9;
  box-shadow: 5px 5px 0 rgba(0, 0, 0, 0.15);
  border-radius: 50%;
  position: absolute;
  z-index: 1; }
  .moon.antar {
    height: 14px;
    right: -10px;
    top: 10%;
    width: 14px;
    -webkit-animation: antar-orbit 5s ease-in-out infinite alternate, revolve 10s ease-in-out infinite;
    -moz-animation: antar-orbit 5s ease-in-out infinite alternate, revolve 10s ease-in-out infinite;
    animation: antar-orbit 5s ease-in-out infinite alternate, revolve 10s ease-in-out infinite; }
  .moon.alaris {
    bottom: 10%;
    left: -20px;
    height: 10px;
    width: 10px;
    -webkit-animation: alaris-orbit 5s ease-in-out infinite alternate, revolve 10s ease-in-out infinite;
    -moz-animation: alaris-orbit 5s ease-in-out infinite alternate, revolve 10s ease-in-out infinite;
    animation: alaris-orbit 5s ease-in-out infinite alternate, revolve 10s ease-in-out infinite; }

.space-junk {
  position: absolute;
  right: -30px;
  top: 20%;
  z-index: 1;
  -webkit-animation: junk-orbit 6s ease-in-out infinite alternate, revolve 12s ease-in-out infinite;
  -moz-animation: junk-orbit 6s ease-in-out infinite alternate, revolve 12s ease-in-out infinite;
  animation: junk-orbit 6s ease-in-out infinite alternate, revolve 12s ease-in-out infinite; }
  .space-junk:after {
    color: #c9c9c9;
    content: "+";
    cursor: default;
    font-size: 40px;
    font-weight: bold; }

@-webkit-keyframes alaris-orbit {
  from {
    left: -20px; }
  to {
    left: 115px; } }

@-moz-keyframes alaris-orbit {
  from {
    left: -20px; }
  to {
    left: 115px; } }

@keyframes alaris-orbit {
  from {
    left: -20px; }
  to {
    left: 115px; } }

@-webkit-keyframes antar-orbit {
  from {
    right: -10px; }
  to {
    right: 115px; } }

@-moz-keyframes antar-orbit {
  from {
    right: -10px; }
  to {
    right: 115px; } }

@keyframes antar-orbit {
  from {
    right: -10px; }
  to {
    right: 115px; } }

@-webkit-keyframes junk-orbit {
  from {
    right: -30px; }
  to {
    right: 130px; } }

@-moz-keyframes junk-orbit {
  from {
    right: -30px; }
  to {
    right: 130px; } }

@keyframes junk-orbit {
  from {
    right: -30px; }
  to {
    right: 130px; } }

@-webkit-keyframes revolve {
  from {
    z-index: 1; }
  to {
    z-index: -1; } }

@-moz-keyframes revolve {
  from {
    z-index: 1; }
  to {
    z-index: -1; } }

@keyframes revolve {
  from {
    z-index: 1; }
  to {
    z-index: -1; } }

@-webkit-keyframes planet-fx {
  from {
    background-position: 0 0; }
  to {
    background-position: 100% 0; } }

@-moz-keyframes planet-fx {
  from {
    background-position: 0 0; }
  to {
    background-position: 100% 0; } }

@keyframes planet-fx {
  from {
    background-position: 0 0; }
  to {
    background-position: 100% 0; } }

@-webkit-keyframes disco {
  0% {
    background-color: #ff3411; }
  20% {
    background-color: #e63a93; }
  40% {
    background-color: #00c1f3; }
  60% {
    background-color: #ff6500; }
  80% {
    background-color: #008f98; }
  100% {
    background-color: #8f3faf; } }

@-moz-keyframes disco {
  0% {
    background-color: #ff3411; }
  20% {
    background-color: #e63a93; }
  40% {
    background-color: #00c1f3; }
  60% {
    background-color: #ff6500; }
  80% {
    background-color: #008f98; }
  100% {
    background-color: #8f3faf; } }

@keyframes disco {
  0% {
    background-color: #ff3411; }
  20% {
    background-color: #e63a93; }
  40% {
    background-color: #00c1f3; }
  60% {
    background-color: #ff6500; }
  80% {
    background-color: #008f98; }
  100% {
    background-color: #8f3faf; } }
